
import { Box, Grid, Paper, styled, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import OIChart from './OIChart';
import StrikePriceList from './StrikePriceList';

import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import OILineChart from './OILineChart';


const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

function ComboBox({ticker, setTicker}) {
  return (
    <Autocomplete
      disablePortal
      id="combo-box-demo"
      options={topp}
      inputValue={ticker}
      value={ticker}
      onChange={(e,nv) => {
        setTicker(nv)
      }}
      sx={{ width: 300 }}
      renderInput={(params) => <TextField {...params} label="Ticker" />}
    />
  );
}

const topp = ['Select','Nifty','BankNifty']


function StrikeAnalysis() {

  const [strikes, setStrikes] = useState([])
  const [ticker, setTicker] = useState('Select')
  
  const [chartdata, setChartdata] = useState({
    labels: ["0", "0", "0", "0", "0", "0"],
    datasets: [
        {
            pointRadius: 0,
            label: "# of Contract",
            data: [0, 0, 0, 0, 0, 0, 0],
            backgroundColor: [
                "rgba(255, 99, 132, 0.2)",
                "rgba(54, 162, 235, 0.2)",
                "rgba(255, 206, 86, 0.2)",
                "rgba(75, 192, 192, 0.2)",
                "rgba(153, 102, 255, 0.2)",
                "rgba(255, 159, 64, 0.2)"
            ],
            borderColor: ["rgba(6, 184, 56, 1)"],
            borderWidth: 4,
            tension: 0.5
        }
    ]
})
const [absolute, setAbsolute] = useState({
  labels: ["0", "0", "0", "0", "0", "0"],
  datasets: [
      {
          label: "No of Contracts",
          data: [0, 0, 0, 0, 0, 0],
          backgroundColor: ["rgba(108, 181, 106, 1)"],
          borderColor: ["rgba(6, 184, 56, 1)"],
          borderWidth: 0,
          borderRadius: 50,
          barThickness: 10
      }
  ]
})

useEffect(() => {
  if(ticker === "Select") return;
  fetch(`http://20.219.2.172:4000/loadstrikes/${ticker}`).then(res => res.json().then((data) => { setStrikes(data) }))
}, [ticker])

  return (
    <Box sx={{ width: '100%', marginTop: '50px' }}>
      <Typography variant='h4'>Strike Analysis</Typography>
      <ComboBox ticker={ticker} setTicker={setTicker}/>
      <StrikePriceList strike={strikes} setAbsolute={setAbsolute} ticker={ticker} setChartdata={setChartdata}/>
      {/* <OIChart data1={data} /> */}
      <Grid container spacing={2} style={{marginTop:'10px'}} >
        <Grid item xs={12} md={12}>
          <Item style={{height:'400px'}}>
            <OILineChart chartdata={chartdata}/>
          </Item>
        </Grid>
        <Grid item xs={12} md={12}>
          <Item style={{height:'400px'}}>
            <OIChart chartdata={absolute}/>
          </Item>
        </Grid>
      </Grid>
    </Box>
  )
}

export default StrikeAnalysis