import { Chip, Grid, Paper, styled, Typography } from '@mui/material';
import React, { useState } from 'react'


//const strikePrice = ['12000', '13000', '14000', '15000', '16000', '17000', '18000', '19000', '20000', '12000', '12000', '12000', '17000', '18000', '19000', '20000', '12000', '12000', '12000', '17000', '18000', '19000', '20000', '12000', '12000', '12000']
const times = ['ALL', '60 MIN', '30 MIN', '15 MIN']
const count = [200, 150, 100, 50]
const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
  height: '120px',
  overflow: 'auto'
}));




function StrikePriceList({ strike, ticker, setChartdata, setAbsolute}) {
  let ab_time = []
  let ab_oi = []
  const [time, setTime] = useState(50)
  const [action, setAction] = useState("")
  const [chooseStrike, setChooseStrike] = useState([])
  return (

    <Grid container>
      <Grid item xs={12}>
        <Item>
          <Typography>STRIKE PRICE</Typography>
          {strike.map((sp, i) => {
            return (<Chip key={i} label={sp} variant='outlined' style={{ marginLeft: '10px', marginTop: '10px' }} onClick={() => { setChooseStrike([sp]) }} />)
          })}
        </Item>
        <Item style={{ marginTop: '10px', height: '60px' }}>
          <Typography>TIME FRAME</Typography>
          {times.map((t, i) => {
            return (<Chip label={t} variant='outlined' style={{ marginLeft: '10px' }} onClick={() => { setTime(count[i]) }} />)
          })}
        </Item>
        <Item style={{ marginTop: '10px', height: '60px' }}>
          <Typography>ACTION</Typography>
          <Chip label="CE" variant='outlined' style={{ marginLeft: '10px' }} onClick={() => { setAction("CE") }} />
          <Chip label="PE" variant='outlined' style={{ marginLeft: '10px' }} onClick={() => { setAction("PE") }} />
          <Chip label="REFRESH" variant='outlined' style={{ marginLeft: '10px' }} onClick={() => {
            fetch(`http://20.219.2.172:4000/strikeanalysis/${ticker}`, {
              method: 'POST',
              headers: {
                'Content-Type': 'application/json'
              },
              body: JSON.stringify({
                "strike": [chooseStrike],
                "timeCount": time,
                "action": action
              })
            }).then(res => res.json().then((data) => {

              for (var i = 0; i <= (data[chooseStrike]['oi'].length - 2); i++) {
                ab_time.push(data[chooseStrike]['time'][i])
                ab_oi.push(data[chooseStrike]['oi'][i] - data[chooseStrike]['oi'][i + 1])
              }
              setChartdata({
                labels: data[chooseStrike]['time'].reverse(),
                datasets: [
                  {
                    pointRadius: 0,
                    label: "# of Contract",
                    data: data[chooseStrike]['oi'].reverse(),
                    backgroundColor: [
                      "rgba(255, 99, 132, 0.2)",
                      "rgba(54, 162, 235, 0.2)",
                      "rgba(255, 206, 86, 0.2)",
                      "rgba(75, 192, 192, 0.2)",
                      "rgba(153, 102, 255, 0.2)",
                      "rgba(255, 159, 64, 0.2)"
                    ],
                    borderColor: ["rgba(6, 184, 56, 1)"],
                    borderWidth: 4,
                    tension: 0.5
                  }
                ]
              })
              setAbsolute({
                labels: ab_time.reverse(),
                datasets: [
                    {
                        label: "No of Contracts",
                        data: ab_oi.reverse(),
                        backgroundColor: ["rgba(108, 181, 106, 1)"],
                        borderColor: ["rgba(6, 184, 56, 1)"],
                        borderWidth: 0,
                        borderRadius: 50,
                        barThickness: 3
                    }
                ]
            })
            }))
          }} />
        </Item>
      </Grid>
      <label>Ticker:{ticker} Strike:{chooseStrike} Action:{action} Time:{time} </label>
    </Grid>


  )
}

export default StrikePriceList