import React, { useEffect, useState } from 'react'
import { styled } from '@mui/material/styles';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import { Typography } from '@mui/material';
import OIChart from './components/OIChart';



const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  }));
function DashContent() {

  const [nifty_options, setNifty_options] = useState({
    labels: ["0","0","0","0","0","0","0"],
    datasets: [
        {
            pointRadius: 0,
            data: [0,0,0,0,0,0,0],
            backgroundColor: [
                "rgba(255, 99, 132, 0.2)",
                "rgba(54, 162, 235, 0.2)",
                "rgba(255, 206, 86, 0.2)",
                "rgba(75, 192, 192, 0.2)",
                "rgba(153, 102, 255, 0.2)",
                "rgba(255, 159, 64, 0.2)"
            ],
            borderColor: ["rgba(6, 184, 56, 1)"],
            borderWidth: 4,
            tension: 0.5
        }
    ]
})

const [banknifty_options, setBankNifty_options] = useState({
  labels: ["0","0","0","0","0","0","0"],
  datasets: [
      {
          pointRadius: 0,
          data: [0,0,0,0,0,0,0],
          backgroundColor: [
              "rgba(255, 99, 132, 0.2)",
              "rgba(54, 162, 235, 0.2)",
              "rgba(255, 206, 86, 0.2)",
              "rgba(75, 192, 192, 0.2)",
              "rgba(153, 102, 255, 0.2)",
              "rgba(255, 159, 64, 0.2)"
          ],
          borderColor: ["rgba(6, 184, 56, 1)"],
          borderWidth: 4,
          tension: 0.5
      }
  ]
})
const [nifty_most_ce, setNifty_most_ce] = useState({
  labels: ["0","0","0","0","0","0","0"],
  datasets: [
      {
          pointRadius: 0,
          data: [0,0,0,0,0,0,0],
          backgroundColor: [
              "rgba(255, 99, 132, 0.2)",
              "rgba(54, 162, 235, 0.2)",
              "rgba(255, 206, 86, 0.2)",
              "rgba(75, 192, 192, 0.2)",
              "rgba(153, 102, 255, 0.2)",
              "rgba(255, 159, 64, 0.2)"
          ],
          borderColor: ["rgba(6, 184, 56, 1)"],
          borderWidth: 4,
          tension: 0.5
      }
  ]
})
const [banknifty_most_ce, setBankNifty_most_ce] = useState({
  labels: ["0","0","0","0","0","0","0"],
  datasets: [
      {
          pointRadius: 0,
          data: [0,0,0,0,0,0,0],
          backgroundColor: [
              "rgba(255, 99, 132, 0.2)",
              "rgba(54, 162, 235, 0.2)",
              "rgba(255, 206, 86, 0.2)",
              "rgba(75, 192, 192, 0.2)",
              "rgba(153, 102, 255, 0.2)",
              "rgba(255, 159, 64, 0.2)"
          ],
          borderColor: ["rgba(6, 184, 56, 1)"],
          borderWidth: 4,
          tension: 0.5
      }
  ]
})
const [nifty_most_pe, setNifty_most_pe] = useState({
  labels: ["0","0","0","0","0","0","0"],
  datasets: [
      {
          pointRadius: 0,
          data: [0,0,0,0,0,0,0],
          backgroundColor: [
              "rgba(255, 99, 132, 0.2)",
              "rgba(54, 162, 235, 0.2)",
              "rgba(255, 206, 86, 0.2)",
              "rgba(75, 192, 192, 0.2)",
              "rgba(153, 102, 255, 0.2)",
              "rgba(255, 159, 64, 0.2)"
          ],
          borderColor: ["rgba(6, 184, 56, 1)"],
          borderWidth: 4,
          tension: 0.5
      }
  ]
})
const [banknifty_most_pe, setBankNifty_most_pe] = useState({
  labels: ["0","0","0","0","0","0","0"],
  datasets: [
      {
          pointRadius: 0,
          data: [0,0,0,0,0,0,0],
          backgroundColor: [
              "rgba(255, 99, 132, 0.2)",
              "rgba(54, 162, 235, 0.2)",
              "rgba(255, 206, 86, 0.2)",
              "rgba(75, 192, 192, 0.2)",
              "rgba(153, 102, 255, 0.2)",
              "rgba(255, 159, 64, 0.2)"
          ],
          borderColor: ["rgba(6, 184, 56, 1)"],
          borderWidth: 4,
          tension: 0.5
      }
  ]
})

useEffect(() => {
  fetch('http://20.219.2.172:4000/idxnifty').then(res => res.json().then((chartdata) => {setNifty_options({
      labels:chartdata.time,
      datasets:[{
          label:"OI",
          data:chartdata.oi,
          backgroundColor: ["rgba(5, 106, 200, 1)"],
          borderColor: ["rgba(6, 184, 56, 1)"],
          borderWidth: 0,
          borderRadius: 50,
          barThickness: 10
      }]
    })}))

    fetch('http://20.219.2.172:4000/idxbnifty').then(res => res.json().then((chartdata) => {setBankNifty_options({
      labels:chartdata.time,
      datasets:[{
          label:"OI",
          data:chartdata.oi,
          backgroundColor: ["rgba(5, 106, 200, 1)"],
          borderColor: ["rgba(6, 184, 56, 1)"],
          borderWidth: 0,
          borderRadius: 50,
          barThickness: 10
      }]
    })
  }))

    
    setTimeout(() => {
      fetch('http://20.219.2.172:4000/nfceoi').then(res => res.json().then((chartdata) => {setNifty_most_ce({
        labels:chartdata.strike,
        datasets:[{
            label:"OI",
            data:chartdata.oi,
            backgroundColor: ["rgba(108, 181, 106, 1)"],
            borderColor: ["rgba(6, 184, 56, 1)"],
            borderWidth: 0,
            borderRadius: 50,
            barThickness: 10
        }]
        
      })}
      ))

      fetch('http://20.219.2.172:4000/bnfceoi').then(res => res.json().then((chartdata) => {setBankNifty_most_ce({
        labels:chartdata.strike,
        datasets:[{
            label:"OI",
            data:chartdata.oi,
            backgroundColor: ["rgba(108, 181, 106, 1)"],
            borderColor: ["rgba(6, 184, 56, 1)"],
            borderWidth: 0,
            borderRadius: 50,
            barThickness: 10
        }]
        
      })
      }
      ))
      
    }, 500);
    setTimeout(() => {
      fetch('http://20.219.2.172:4000/nfpeoi').then(res => res.json().then((chartdata) => {setNifty_most_pe({
        labels:chartdata.strike,
        datasets:[{
            label:"OI",
            data:chartdata.oi,
            backgroundColor: ["rgba(220, 112, 112, 1)"],
            borderColor: ["rgba(6, 184, 56, 1)"],
            borderWidth: 0,
            borderRadius: 50,
            barThickness: 10
        }]
        
      })
      }
      ))

      fetch('http://20.219.2.172:4000/bnfpeoi').then(res => res.json().then((chartdata) => {setBankNifty_most_pe({
        labels:chartdata.strike,
        datasets:[{
            label:"OI",
            data:chartdata.oi,
            backgroundColor: ["rgba(220, 112, 112, 1)"],
            borderColor: ["rgba(6, 184, 56, 1)"],
            borderWidth: 0,
            borderRadius: 50,
            barThickness: 10
        }]
        
      })
      }
      ))
    }, 1000);
    
}, [])

  return (
    <Box sx={{ width: '100%', marginTop:'50px' }}>
      
      <Typography variant='h4'>Dashboard</Typography>
      <Grid container rowSpacing={1} columnSpacing={{ xs: 2, sm: 2, md: 3 }}>
        <Grid item xs={6}>
          <Typography>Nifty OI</Typography>
          <Item sx={{height:'200px'}}>
            <OIChart chartdata={nifty_options}/>
          </Item>
        </Grid>
        <Grid item xs={6}>
        <Typography>BankNifty OI</Typography>
          <Item sx={{height:'200px'}}>
          <OIChart chartdata={banknifty_options} />
          </Item>
        </Grid>
        <Grid item xs={6}>
        <Typography>Nifty OI CE</Typography>
          <Item sx={{height:'200px'}}>
          <OIChart chartdata={nifty_most_ce}/>
          </Item>
        </Grid>
        <Grid item xs={6}>
        <Typography>BankNifty OI CE</Typography>
          <Item sx={{height:'200px'}}>
          <OIChart chartdata={banknifty_most_ce}/>
          </Item>
        </Grid>
        <Grid item xs={6}>
        <Typography>Nifty OI PE</Typography>
          <Item sx={{height:'200px'}}>
          <OIChart chartdata={nifty_most_pe}/>
          </Item>
        </Grid>
        <Grid item xs={6}>
        <Typography>BankNifty OI PE</Typography>
          <Item sx={{height:'200px'}}>
          <OIChart chartdata={banknifty_most_pe}/>
          </Item>
        </Grid>
      </Grid>
    </Box>
  )
}

export default DashContent