import { Autocomplete, TextField } from '@mui/material';
import React from 'react'

const topp = ['Select','Nifty','BankNifty']

function Selector({ticker, setTicker}) {
    return (
        <Autocomplete
          disablePortal
          id="combo-box-demo"
          options={topp}
          inputValue={ticker}
          value={ticker}
          onChange={(e,nv) => {
            setTicker(nv)
          }}
          sx={{ width: 300 }}
          renderInput={(params) => <TextField {...params} label="Ticker" />}
        />
      );

}

export default Selector