import React from 'react'

import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  BarElement,
  LineElement
} from "chart.js";
import { Bar } from "react-chartjs-2";

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  BarElement,
  LineElement
);





const options = {
  responsive: true,
  maintainAspectRatio: false,
  scales: {
    y: {
      beginAtZero: true,
      grid: {
        display: true,
        drawBorder: true
      },
      ticks: {
        callback: function (value, index, ticks) {
          return value + "k";
        }
      }
    },
    x: {
      grid: {
        display: false,
        drawBorder: false
      },
      ticks: {
        display: true
      }
    }
  },
  plugins: {
    legend: {
      display:false
    },
    title: {
      display: false,
    }
  }
};



function OIChart({chartdata}) {
  return (
    
      <Bar data={chartdata} options={options} /> 
    
  )
}

export default OIChart
