import React from 'react'
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement
  } from "chart.js";
  import { Line } from "react-chartjs-2";
  
  ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement
  );

  const options = {
    responsive: true,
    maintainAspectRatio: false
  };
function OILineChart({chartdata}) {
  return (
    <Line data={chartdata} options={options}/>
  )
}

export default OILineChart