import { Box, Chip, Typography } from '@mui/material';
import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import { Line } from 'react-chartjs-2';
import { styled } from '@mui/material/styles';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import Selector from './Selector';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
);


const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
  height: '120px',
  overflow: 'auto'
}));

var dynamicColors = function () {
  var r = Math.floor(Math.random() * 255);
  var g = Math.floor(Math.random() * 255);
  var b = Math.floor(Math.random() * 255);
  var a = Math.floor(Math.random() * 255);
  return "rgba(" + r + "," + g + "," + b + "," + a + ")";
};

const options = {
  responsive: true,
  maintainAspectRatio: false,
  scales: {
    y: {
      ticks: {
        callback: function (value, index, ticks) {
          return value + "k";
        }
      }
    }
  },
  plugins: {
    interaction: {
      mode: 'datasets'
    }
  }
}
const times = ['ALL', '60 MIN', '30 MIN', '15 MIN']
const count = [200, 150, 100, 50]
function Trending() {

  let ce_data = []
  let pe_data = []
  const [strikes, setStrikes] = useState([])
  const [ticker, setTicker] = useState('Select')
  const [time, setTime] = useState(50)

    const [strike_range, setStrike_range] = useState([])
    const [ce, setCe] = useState({
        labels: ["Red", "Blue", "Yellow", "Green", "Purple", "Orange"],
        datasets: [
            {
                pointRadius: 0,
                label: "# of Contract",
                data: [0, 0, 0, 0, 0, 0, 0],
                backgroundColor: [
                    "rgba(255, 99, 132, 0.2)",
                    "rgba(54, 162, 235, 0.2)",
                    "rgba(255, 206, 86, 0.2)",
                    "rgba(75, 192, 192, 0.2)",
                    "rgba(153, 102, 255, 0.2)",
                    "rgba(255, 159, 64, 0.2)"
                ],
                borderColor: [dynamicColors()],
                borderWidth: 4,
                tension: 0.5
            }]
    })
    const [pe, setPe] = useState({
        labels: ["Red", "Blue", "Yellow", "Green", "Purple", "Orange"],
        datasets: [
            {
                pointRadius: 0,
                label: "# of Contract",
                data: [0, 0, 0, 0, 0, 0, 0],
                backgroundColor: [
                    "rgba(255, 99, 132, 0.2)",
                    "rgba(54, 162, 235, 0.2)",
                    "rgba(255, 206, 86, 0.2)",
                    "rgba(75, 192, 192, 0.2)",
                    "rgba(153, 102, 255, 0.2)",
                    "rgba(255, 159, 64, 0.2)"
                ],
                borderColor: [dynamicColors()],
                borderWidth: 4,
                tension: 0.5
            }]
    })
    let fce_data = {}
  useEffect(() => {
     if (ticker === "Select") return;
     fetch(`http://20.219.2.172:4000/loadstrikes/${ticker}`).then(res => res.json().then((data) => { setStrikes(data) }))
   }, [ticker])

  return (
    <Box sx={{ width: '100%', marginTop: '50px' }}>

      <Typography variant='h4'>Trending</Typography>
      <Selector ticker={ticker} setTicker={setTicker} />
      <Grid container spacing={2}>
      
        <Grid item xs={12} md={12}>
          <Item>
            <Typography>STRIKE PRICE</Typography>
            {
            strikes.length <= 0 ? <Typography>SELECT TICKER</Typography>:
            strikes.map((sp, i) => {
              return (<Chip key={i} label={sp} variant='outlined' style={{ marginLeft: '10px', marginTop: '10px' }} onClick={() => { setStrike_range(oa => [...oa,sp]) }} />)
            })}
          </Item>
          <Item style={{ marginTop: '10px', height: '60px' }}>
          <Typography>TIME FRAME</Typography>
          {times.map((t, i) => {
            return (<Chip label={t} variant='outlined' style={{ marginLeft: '10px' }} onClick={() => { setTime(count[i]) }} />)
          })}
          <Chip label="REFRESH" variant='outlined' style={{ marginLeft: '10px' }} onClick={() => { 
           
              fetch(`http://20.219.2.172:4000/trending/Nifty`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    "strike": strike_range,
                    "timeCount": time,
                    "action": "CE"
                })
            }).then(res => res.json().then((chartdata) => {
    
                for (var i = 0; i <= strike_range.length - 1; i++) {
                    var color = dynamicColors()
                    var ab = [0]
                    var c = [0]
                    var a = chartdata[strike_range[i]]['oi'].reverse()
                    for (var j = 1; j <= time; j++) {
                        ab.push((a[j] - a[j - 1]) / 1000)
                        c.push(c[j - 1] + ab[j])
                    }
                    ce_data.push(
                        {
                            label: strike_range[i],
                            pointRadius: 0,
                            data: c,
                            backgroundColor: [
                                "rgba(255, 99, 132, 0.2)",
                                "rgba(54, 162, 235, 0.2)",
                                "rgba(255, 206, 86, 0.2)",
                                "rgba(75, 192, 192, 0.2)",
                                "rgba(153, 102, 255, 0.2)",
                                "rgba(255, 159, 64, 0.2)"
                            ],
                            borderColor: [color],
                            borderWidth: 3,
                            tension: 0.5
                        }
                    )
                }
    
                fce_data['labels'] = chartdata[strike_range[0]]['time'].reverse()
                fce_data['datasets'] = ce_data
                setCe(fce_data)
    
            }))
            var fpe_data = {}
    
            setTimeout(() => {
              fetch(`http://20.219.2.172:4000/trending/Nifty`, {
                  method: 'POST',
                  headers: {
                      'Content-Type': 'application/json'
                  },
                  body: JSON.stringify({
                      "strike": strike_range,
                      "timeCount": time,
                      "action": "PE"
                  })
              }).then(res => res.json().then((chartdata) => {
    
                  for (var i = 0; i <= strike_range.length - 1; i++) {
                      var color = dynamicColors()
                      var ab = [0]
                      var c = [0]
                      var a = chartdata[strike_range[i]]['oi'].reverse()
                      for (var j = 1; j <= time; j++) {
                          ab.push((a[j] - a[j - 1]) / 1000)
                          c.push(c[j - 1] + ab[j])
                      }
                      pe_data.push(
                          {
                              label: strike_range[i],
                              pointRadius: 0,
                              data: c,
                              backgroundColor: [
                                  "rgba(255, 99, 132, 0.2)",
                                  "rgba(54, 162, 235, 0.2)",
                                  "rgba(255, 206, 86, 0.2)",
                                  "rgba(75, 192, 192, 0.2)",
                                  "rgba(153, 102, 255, 0.2)",
                                  "rgba(255, 159, 64, 0.2)"
                              ],
                              borderColor: [color],
                              borderWidth: 3,
                              tension: 0.5
                          }
                      )
                  }
    
                  fpe_data['labels'] = chartdata[strike_range[0]]['time'].reverse()
                  fpe_data['datasets'] = pe_data
                  setPe(fpe_data)
    
              }))
          }, 2000)
           }
           }  />
        </Item>
        </Grid>
        <Grid item xs={12} md={12}>
          <Item style={{height:'60px'}}>
            <Typography>SELECTED STRIKES</Typography>
            {
            strike_range.length <= 0 ? <Typography>SELECT STRIKES</Typography>:
            strike_range.map((sr,i) => {
              return(
                <Chip label={sr} key={i} variant="outlined" onDelete={() => {setStrike_range(prev => prev.filter(sp => sp !== sr))}}/>
              )
            })}
          </Item>
        </Grid>
        <Grid item xs={12} md={12}>
        <Typography>CE(K)</Typography>
          <Item style={{height:'400px'}}>
            <Line data={ce} options={options}/>
          </Item>
        </Grid>
        <Grid item xs={12} md={12}>
          <Typography>PE(K)</Typography>
          <Item style={{height:'400px'}}>
         <Line data={pe} options={options}/>
          </Item>
        </Grid>
      </Grid>

    </Box>
  )
}

export default Trending