import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { Chip } from '@mui/material';

function createData(time, oi, price, action) {
  return { time, oi, price, action };
}

const rows = [
  createData('9:15:00 AM', 159, 6.0, 'Short BuildUp'),
  createData('9:20:00 AM', 159, 6.0, 'Long BuildUp'),
  createData('9:25:00 AM', 159, 6.0, 'Short Covering'),
  createData('9:30:00 AM', 159, 6.0, 'Long Unwinding'),
  createData('9:35:00 AM', 159, 6.0, 'Short Covering'),
  createData('9:40:00 AM', 159, 6.0, 'Long Unwinding'),
  createData('9:45:00 AM', 159, 6.0, 'Short Covering'),
  createData('9:50:00 AM', 159, 6.0, 'Long Unwinding'),
  createData('9:55:00 AM', 159, 6.0, 'Short Covering'),
];

export default function DataTable() {
  return (
    <TableContainer component={Paper} sx={{ marginTop: '10px' }} >
      <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell>Time</TableCell>
            <TableCell align="right">OI</TableCell>
            <TableCell align="right">Price</TableCell>
            <TableCell align="right">Action</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row, i) => (
            <TableRow
              key={i}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row">
                {row.time}
              </TableCell>
              <TableCell align="right">{row.oi}</TableCell>
              <TableCell align="right">{row.price}</TableCell>
              <TableCell align="right">{row.action === "Long Unwinding" || row.action === "Short BuildUp" ? <Chip label={row.action} size='small' color='error' /> : <Chip size='small' label={row.action} color='success' />}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
